<script>
import axios from "axios";
export default {
  data() {
    return {
      bannerList: [],
      noticeList: [],
      policyList: [], //政策
      newsList: [],
      teacherList: [],
      messageList: [],
    };
  },
  mounted() {},
  created() {
    this.InitPage();
    this.loadData();
  },
  methods: {
    InitPage: function () {
      axios.get("/api/index").then((d) => {
        // console.log(d);
        let result = d.data;
        console.log(result);
        if (result.code == 200) {
          this.bannerList = result.data.bannerList;
          this.noticeList = result.data.noticeList;
          this.policyList = result.data.policyList;
          this.newsList = result.data.newsList;
          this.teacherList = result.data.teacherList;
          this.policyList.length = 1;
          this.newsList.length = 1;
          setTimeout(() => {
            this.renderBanner();
          }, 500);
        }
      });
    },
    loadData: function () {
      axios.get("/api/index/msg/find").then((d) => {
        // console.log(d.data);
        if (d.data.code == 200) {
          // this.currpage = "list";
          let result = d.data.data;
          console.log(result);
          this.messageList = result;
          // for (let ii in result) {
          //   result[ii].hs = false;
          // }
          // this.list = result;
          // if (this.list.length > 0) {
          //   this.data = this.list[0];
          // }
        }
      });
    },
    mbar: function (sobj) {
      var docurl = sobj.options[sobj.selectedIndex].value;
      if (docurl != "") {
        open(docurl, "_blank");
        sobj.selectedIndex = 0;
        sobj.blur();
      }
    },
    renderBanner: () => {
      $(".public_list").slide();
      $(".news_banner").slide({
        mainCell: ".bd ul",
        autoPlay: true,
        effect: "leftLoop",
        mouseOverStop: false,
        interTime: 5000,
      });
    },
  },
};
</script>


